#tabs {
	margin: 1rem 0 2rem;
	display: flex;
	flex-wrap: wrap;
	gap: 0; // ไม่มีช่องว่างระหว่างแท็บ
	justify-content: center;
	// border: 1px solid #ccc;
	// border-radius: 6px;
	overflow: hidden;
	background: #f8fafd;
}

.tab {
	padding: 12px 28px;
	background-color: #f8fafd;
	border: none;
	border-right: 1px solid #ccc;
	border-radius: 0;
	font-weight: 500;
	cursor: pointer;
	transition:
		background 0.2s,
		color 0.2s,
		border 0.2s;
	box-shadow: none;
	// color: #333;
	outline: none;

	// ลบ border ขวาของแท็บสุดท้าย
	&:last-child {
		border-right: none;
	}
}

.tab:hover,
.tab:focus,
.tab.active {
	background-color: #e0ecff;
	color: #2874fc;
	z-index: 2;
}

@media (max-width: 600px) {
	#tabs {
		flex-direction: column;
		border-radius: 8px;
		border: 1px solid #ccc;
		overflow: hidden;
	}
	.tab {
		border-right: none;
		border-bottom: 1px solid #ccc;
		border-radius: 0;
		padding: 14px 18px;
		width: 100%;
		text-align: left;
		background: #f8fafd;
		transition:
			background 0.2s,
			color 0.2s;
	}
	.tab:last-child {
		border-bottom: none;
	}
	.tab.active,
	.tab:hover,
	.tab:focus {
		background-color: #e0ecff;
		color: #2874fc;
		z-index: 2;
	}
}

.our-search input[type="text"] {
	width: 100%;
	padding: 12px 16px;
	border: 1px solid #ccc;
	border-radius: 6px;
	font-size: 1rem;
	transition:
		border-color 0.2s,
		box-shadow 0.2s;
	box-sizing: border-box;
	margin-bottom: 1rem;
}

.our-search input[type="text"]:focus {
	border-color: #2874fc;
	box-shadow: 0 0 0 2px rgba(40, 116, 252, 0.15);
	outline: none;
}

.results {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
	gap: 1rem;
}

.card {
	background: #fff;
	border: 1px solid #ddd;
	border-radius: 6px;
	text-decoration: none;
	color: inherit;
	overflow: hidden;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
	transition:
		transform 0.2s ease,
		box-shadow 0.3s ease;
}

.card:hover {
	transform: translateY(-4px);
	box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

.card-image {
	width: 100%;
	height: 180px;
	object-fit: cover;
}

.card-body {
	padding: 1rem;
}

.card-title {
	font-size: 1rem;
	margin-bottom: 0.5rem;
}

.card-excerpt {
	font-size: 0.875rem;
	color: #555;
}

#pagination {
	margin-top: 1.5rem;
	display: flex;
	flex-wrap: wrap;
	gap: 0.5rem;
	justify-content: center;
}

/* pagination buttons */
.pagination-btn {
	background: #eee;
	border: 1px solid #ccc;
	padding: 8px 12px;
	border-radius: 50%;
	min-width: 40px;
	min-height: 40px;
	text-align: center;
	line-height: 1.2;
	font-weight: bold;
	cursor: pointer;
	transition: all 0.3s ease;
}

.pagination-btn:hover {
	background-color: #d0e2ff;
	border-color: #2874fc;
}

.pagination-btn.active {
	background-color: #fff;
	color: #2874fc;
	border-color: #6392e2;
}
